.TodoItem{
    border-radius: 4px;
    margin: 1px;
    background-color: #fff;
    padding: 1em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    font-size: 15px;
}

.TodoItem>input[type="checkbox"]{
    display: none;
}

.TodoItem>input[type="checkbox"]+label{
    text-decoration: none;
}

.TodoItem>.title{
    -ms-flex-positive: 1;
        flex-grow: 1;
    padding: 0 0.5em;
}

.TodoItem>.completed~.title{
    text-decoration: line-through;
    color: #999;
}

.TodoItem>button{
    border: none;
    background-color: transparent;
}

.TodoItem i{
    color: #5b7a59;
    cursor: pointer;
}

.TodoItem i:hover{
    opacity: 0.7;
}


