.UserDialog-Wrapper {
    /*全屏*/
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-image: url('images/bg-sign.svg');
    background-attachment: fixed;
    background-position: center;
    background-size: 100vw;
    background-color: cornflowerblue;

    /*内容居中*/
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: start;
        align-items: flex-start;
}

.UserDialog{
    margin-top: 100px;
    padding-top: 30px;
    min-width: 20em;
    border: 1px solid #ccc;
    background-color: white;
    border-radius: 4px;
    -webkit-box-shadow: 0 0 1em hsla(0, 0%, 0%, 0.35);
            box-shadow: 0 0 1em hsla(0, 0%, 0%, 0.35);
    padding-bottom: 30px;
}

.UserDialog>h1{
    text-align: center;
    color: #db4c3f;
    font-size: 2.5em;
}

.UserDialog .panes{
    padding: 0 1em 1em;
}

.UserDialog form>.row{
    display: -ms-flexbox;
    display: flex;
    padding: 0.3em 0;
}

.UserDialog form>.row>input[type="text"],
.UserDialog form>.row>input[type="password"]{
    display: block;
    width: 75%;
    margin: 0 auto;
    padding: 8.5px 0;
    border: 1px solid #ff9999;
    -webkit-box-shadow: inset 0 0 2px 0 #ffaaaa;
            box-shadow: inset 0 0 2px 0 #ffaaaa;
    border-radius: 2px;
    padding-left: 5px;
}

.UserDialog form>.row>input[type="text"]:focus,
.UserDialog form>.row>input[type="password"]:focus{
    outline-color: #ffcc99;
}

.UserDialog form>.row.action{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-direction: column;
        flex-direction: column;
}

.UserDialog form>.action>button{
    cursor: pointer;
    background-color: #db4c3f;
    color: #fff;
    border: none;
    padding: 4px 0;
    border-radius: 3px;
    display: block;
    width: 75%;
    margin: 0 auto;
    line-height: 2em;
}

.UserDialog form>.action>button:hover{
    opacity: 0.7;
}

.UserDialog form>.action>button+a{
    display: block;
    -ms-flex-item-align: start;
        align-self: flex-start;
    margin-left: 36px;
    margin-top: 10px;
}

.UserDialog .forgotPassword > h3 {
    padding: 1em;
    text-align: center;
}
.UserDialog .forgotPassword > form {
    padding: 0 1em 1em;
}


